import React from 'react';

/*components*/
import AppBar from 'material-ui/AppBar';
import { Tabs, Tab } from 'material-ui/Tabs';
import Badge from 'material-ui/Badge';
import IconButton from 'material-ui/IconButton';

import IconPower from 'material-ui/svg-icons/action/power-settings-new';
import IconPerson from 'material-ui/svg-icons/social/person';

import NotificationsIcon from 'material-ui/svg-icons/social/notifications';
//import history from 'react-router/lib/hashHistory';
import createHistory from 'history/createBrowserHistory'
/*Style*/
import styled from 'styled-components';

const Header = styled.header`
    height: 100%;
    height: 60px;
    min-height: 60px;
    display: flex !important;
    position: relative;
    justify-content: space-between;
    align-items: center;
    background-color: #fafafa;

    hgroup {
        display: flex;
        justify-content: flex-start;
        height: 100%;
        min-height: 60px;

        .containerMenuHeader {
            width: 166px !important;
            height: 86px !important;
            padding: 0 !important;
            background: #fff !important;
            position: absolute !important;
            top: 56px !important;
            z-index: 9999 !important;
            right: -20px !important;
            display: none !important;
            box-shadow: 0px 10px 25px -10px #888888;

            .menuDropProfile {
                list-style: none !important;
                padding: 0px !important;
                height: 86px !important;
                li {
                    width: 166px !important;
                    min-height: 43px !important;
                    &:last-child {
                        a {
                            border-bottom: none !important;
                        }
                    }
                    a {
                        text-align: left !important;
                        display: block !important;
                        width: 166px !important;
                        min-height: 43px !important;
                        line-height: 43px !important;
                        padding: 0 12px !important;
                        color: #757575 !important;
                        font-size: 12px !important;
                        border-bottom: 1px solid #f4f4f4;
                        &:hover {
                            background: #fafafa !important;
                            text-decoration: none !important;
                        }
                        svg {
                            width: 22px !important;
                            height: 22px !important;
                            margin: 0 2px 0 0 !important;
                            padding: 3px 0 0 0 !important;
                            transform: translateY(4px) !important;
                        }
                    }
                }
            }
        }
    }

    /*Logo Santander*/
    .logo-santander {
        width: 134.5px;
        height: 25px;
        margin: 15px 77px 20px 20px;
        display: inline-block;
        object-fit: contain;
        background: no-repeat
            url(data:image/svg+xml;base64,);
        background-position: center center;
        background-size: 134.5px 25px;
        cursor: pointer;
    }

    /*Icone de Busca/Pesquisa*/
    .search {
        width: 25.4px;
        height: 100%;
        min-height: 60px;
        background-position: center center !important;
        background: no-repeat
            url(data:image/svg+xml;base64,);
        background-size: 24px 24px;
        position: relative;
        cursor: pointer;
        transition: background-size 0.3s ease;
        margin: 0 15px 0 0;
        &:before {
            content: "";
            display: inline-block;
            width: 25.4px;
            height: 2px;
            background-color: #e00303;
            position: absolute;
            bottom: 0;
        }

        &:hover {
            background-size: 27px 27px;
        }
    }

    /*Menu-TABS*/
    .header-tabs {
        min-width: 430px;
        height: 100%;
        min-height: 60px;
        > div:nth-child(1) {
            height: 100%;
            min-height: 60px;
            background-color: transparent !important;
            button {
                width: auto;
                font-family: OpenSans;
                font-size: 14px;
                text-align: left;
                color: #757575 !important;
                margin: 0 15 !important;
                text-transform: none !important;
            }
        }
        > div:nth-child(2) {
            height: 100%;
            min-height: 60px;
            div {
                background-color: #e00303 !important;
            }
        }
    }

    /*Profile*/
    .profile-user {
        height: 100%;
        min-height: 60px;
        display: flex;
        align-items: center;
        justify-content: flex-end;

        > div {
            padding: 0 !important;
            svg {
                color: #e00303 !important;
            }
            > span {
                transform: scale(0.7);
                background-color: #fafafa !important;
                color: #e00303 !important;
                border: 1px solid #e00303 !important;
                align-items: normal !important;
                padding-top: 2px !important;
            }
        }

        .menu-profile {
            &:hover {
                .containerMenuHeader {
                    display: inline-block !important;
                }
            }
            display: inline-block;
            height: 100%;
            min-height: 60px;
            position: relative;
            cursor: pointer;
            margin: 0 38px 0 18px;
            line-height: 60px;
            font-family: Roboto, Helvetica, sans-serif;
            font-size: 12px;
            font-weight: bold !important;
            text-align: right;
            color: #980000 !important;
            text-decoration: none;
            &:after {
                content: "";
                display: inline-block;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: -25px;
                width: 16px;
                height: 16px;
                background-size: 16px 16px;
                background-image: url(data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCIgdmlld0JveD0iMCAwIDI0IDI0Ij4gICAgPGcgZmlsbD0ibm9uZSIgZmlsbC1ydWxlPSJldmVub2RkIj4gICAgICAgIDxwYXRoIGQ9Ik0yNCAwdjI0SDBWMHoiLz4gICAgICAgIDxwYXRoIGZpbGw9IiNFMDAzMDMiIGQ9Ik0xOSA4SDVsNyAxMXoiLz4gICAgPC9nPjwvc3ZnPg==);
            }
        }
    }
`;

const BadgeStyled = styled(Badge)`
    &.stylehide{
        > span{
                display:none!important;
        }
    }
    &.maior99{
        > span{
                padding:0 4px!important;
                width:auto!important;
                left:calc(100% - 20px)!important;
                right:auto!important;
                border-radius: 10px!important;
                transform-origin:left center;
        }
    }

`;

class AppBarIcon extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            slideIndex: this.props.initialMenu,
            badgeNumber: 0
        };
    }

  
    // handleChange = (value) => {
    //     this.setState({
    //         slideIndex: value
    //     });
    // };

    render() {
        const notificationNumber = 100;

        return (
            <Header>
                <hgroup>
                    <a className="logo-santander" />
                    {/* <a className="search" /> */}
                    <Tabs
                        onChange={this.handleChange}
                        value={this.state.slideIndex}
                        className={'header-tabs'}
                    >
                        <Tab
                            label="Banco de Ativos"
                            onActive={() =>
                                (window.location.href =
                                    'http://www.google.com.br')}
                            value={0}
                            className={
                                this.state.stepIndex == 2 ? 'active' : ''
                            }
                        />
                        <Tab
                            label="Gestão de Campanhas"
                            onActive={() =>
                                (window.location.href =
                                    'http://www.google.com.br')}
                            value={1}
                            className={
                                this.state.stepIndex == 1 ? 'active' : ''
                            }
                        />
                        <Tab
                            label="Budget Manager"
                            value={2}
                            className={
                                this.state.stepIndex == 0 ? 'active' : ''
                            }
                        />
                    </Tabs>
                </hgroup>
                <hgroup>
                    <div className="profile-user">
                        <BadgeStyled
                            badgeContent={notificationNumber}
                            secondary={true}
                            badgeStyle={{ top: 0, right: 0 }}
                            className={
                                notificationNumber > 99
                                    ? 'maior99'
                                    : '' && notificationNumber == 0
                                      ? 'stylehide'
                                      : ''
                            }
                        >
                            <IconButton href="www.google.com.br">
                                <NotificationsIcon />
                            </IconButton>
                        </BadgeStyled>
                        <a className="menu-profile">
                            {'Emerson'}

                            <div className="containerMenuHeader">
                                <ul className="menuDropProfile">
                                    <li>
                                        <a
                                            onClick={() =>
                                                (window.location.href =
                                                    'http://www.google.com.br')}
                                        >
                                            <IconPerson /> Editar Perfil
                                        </a>
                                    </li>
                                    <li>
                                        <a
                                            onClick={() =>(window.location.href =
                                                'http://www.google.com.br')}
                                        >
                                            <IconPower /> Sair
                                        </a>
                                    </li>
                                </ul>
                            </div>
                        </a>
                    </div>
                </hgroup>
            </Header>
        );
    }
}
export default AppBarIcon;



