import React from 'react';
//import history from 'react-router/lib/hashHistory';
import createHistory from 'history/createBrowserHistory'

/*Components*/
import { Step, Stepper, StepButton } from 'material-ui/Stepper';
import FlatButton from 'material-ui/FlatButton';

/*Icons*/
import ActionHomeIcon from 'material-ui/svg-icons/action/home';
import ArrowForwardIcon from 'material-ui/svg-icons/hardware/keyboard-arrow-right';

/*Style*/

import styled, { css } from 'styled-components';

const BreadCrumb = styled.div`
    height: 100%;
    height: 60px;
    min-height: 60px;
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid rgb(224, 224, 224);
    border-bottom: 1px solid rgb(224, 224, 224);
    background:#fff!important;
    overflow: visible!important;
    z-index: 100;
    .breadcrumb-part{
        height: 60px;
        max-height: 60px;
        overflow:hidden;
        position:relative;

        >div{
            position:relative;
            top:-5px;
        }

        button{
            &.active{
                span{
                    font-size:14px;
                    color:#980000!important;
                    font-weight:bold!important;
                }
            }
            span{
                color:#757575!important;
            }
        }
    }

}
`;

const StyledStepButton = styled(StepButton)`
    &.active{
        span{
            font-size:14px;
            color:#980000!important;
            font-weight:bold!important;
        }
        svg{
            fill:#980000!important;
        }
    }
`;

const BreadCrumbButtonGroup = styled.div`
    display: flex;
    align-items: center;
    padding: 0 10px;
`;

const BudgetButtonPrimary = styled(FlatButton)`
    background: #1dc3c8!important;
    border:1px solid #1dc3c8!important;
    color: #ffffff!important;
    max-width:172px!important;
    margin: 0 10px!important;
    padding:0 36px!important;
    box-sizing:border-box!important;

    div{
        display:inline-block!important;
        width:100%!important;
        height: 100%!important;

        span{
            font-size:12px!important;
            padding:0!important;
        }
    }
`;

const BudgetButtonOutline = styled(FlatButton)`
    background: #fff!important;
    border:1px solid #1dc3c8!important;
    color: #1dc3c8!important;
    max-width:auto!important;
    margin: 0 10px!important;
    padding:0 36px!important;

    div{
        display:inline-block!important;
        width:100%!important;
        height: 100%!important;

        span{
            font-size:12px!important;
            padding:0!important;
        }
    }
`;

class PageBreadCrumb extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            stepIndex: this.props && this.props.initialItem ?
            this.props.initialItem : 1,
            badgeNumber: 0
        };
    }

    render() {

        //const { stepIndex } = this.state;

        const stepIndex = this.props && this.props.initialItem ?
        this.props.initialItem : 1
        console.log('this.state', this.state)
        const {
            steps
        } = this.props;

        return (

            <BreadCrumb>
                <div className="breadcrumb-part">
                    <Stepper
                        linear={false}
                        connector={<ArrowForwardIcon color="#e00303" />}
                        activeStep={stepIndex}
                    >
                        <Step>
                            <StyledStepButton
                                onClick={() => this.setState({ stepIndex: 0 })}
                                icon={
                                    <ActionHomeIcon
                                        color="#9e9e9e"
                                        hoverColor="#b71c1c"
                                    />
                                }
                                className={
                                    this.state.stepIndex == 0 ? 'active' : ''
                                }
                            />
                        </Step>

                        {steps.map((step, index) => {
                            return (
                                <Step key={index}>
                                    <StyledStepButton
                                        onClick={() => (
                                            this.setState({
                                                stepIndex: step.index
                                            }),
                                            createHistory.push({
                                                pathname: step.pathname
                                            })
                                        )}
                                        icon={null}
                                        className={
                                            this.state.stepIndex == step.index
                                                ? 'active'
                                                : ''
                                        }
                                    >
                                        {step.title}
                                    </StyledStepButton>
                                </Step>
                            );
                        })}
                    </Stepper>
                </div>
            </BreadCrumb>


        );
    }
}

export default PageBreadCrumb;



