import React from 'react';

const styles = {
    root: {
        width: '100%' /*1280px*/,
        height: 'auto',
        backgroundColor: '#fafafa'
    }
};

const PageWrapper = ({ children }) =>
    <div style={styles.root}>
        {children}
    </div>;

export default PageWrapper;



