import React from 'react';
import AppBar from 'material-ui/AppBar';
import { connect } from 'react-redux'
import PageWrapper from '../../components/page-wrapper';
import BreadCrumbDinamic from '../../components/bread-crumb-dinamic';
import MenuBar from '../../components/menu-bar';
import HeaderBar from '../../components/header-bar';

/**
 * A simple example of `AppBar` with an icon on the right.
 * By default, the left icon is a navigation-menu.
 */
class Home extends React.Component {
    componentDidMount()
    {
        console.log('addTodo', this.props.addTodo)

        var today = new Date();
        var day = today.getDay();
         this.props.addTodo(today)
    }
    render() {
                //índice "0" é o Home
                const breadCrumbSteps = [
                    {
                        index: 1,
                        title: 'Gerenciamento de Orçamentos',
                        pathname: 'budget-manager/budget'
                    },
                    {
                        index: 2,
                        title: 'Ações',
                        pathname: 'budget-manager/actions-parts'
                    },
                    {
                        index: 3,
                        title: 'Dell',
                        pathname: 'budget-manager/actions-parts-details'
                    }
                ];
                
        console.log('props', this.props)
        const titulo = this.props.bar ? this.props.bar.toString() : 'vazio'
        console.log('titulo', titulo)
        console.log('breadCrumbSteps', breadCrumbSteps)
        //this.props.bar ? this.props.bar :
        return (
            <PageWrapper>
                                    <HeaderBar profileUser="Camila Bueno" initialMenu={2} />

<BreadCrumbDinamic
    initialItem={3}
    lastPath={'Ações'}
    module={'ActionsParts'}
    
    steps={breadCrumbSteps}
/>
                <AppBar
                title={titulo}
                iconClassNameRight="muidocs-icon-navigation-expand-more"
                />
            </PageWrapper>
        )
    }
}

export default Home


